.itemSecurityCenter{
  width: 100%;
  height: auto;
  border: 1px solid transparent;
  border-top-color: #d5d5d6;
  border-bottom-color: #d5d5d6;
}

.subItemSecurityCenter{
  height: 150px;
  display: grid;
  grid-template-columns: 12vw 1fr;
  margin: 15px 0;
  position: relative;
}

.subItemSecurityCenter div, .subItemSecurityCenter p{
  /*border: 1px solid red;*/
  margin: 0 !important;
}

.contentSubItem{
  display: grid;
  grid-template-columns: 1fr 250px;
  border: 1px solid transparent;
  border-bottom-color: #d5d5d6;
  /*border-top-color: #d5d5d6;*/
}

.contCtaMovilSec{
  position: absolute;
  right: 0;
  height: 100%;
  width: 40px;
  display: grid;
  align-items: center;
  justify-items:center;
}

.contCtaMovilSec i{
  /*color: gray;*/
  font-size: 18px;
}



.contentSubItem.last{
  border-bottom-color: transparent;

}

.SCdesc{
  font-size: 15px;
}


.rotateGono{
  animation-name: rotateLoader;
  animation-duration: .65s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in;
}

@keyframes rotateLoader{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(357deg);
  }
}





.contentSubItem p{
  /*margin: 15px 10px !important;*/
  margin: 0 10px !important;
}

.SCverification{
  align-self: center;
  /*color: #59B200;*/
  font-size: 15px;
}

.SCverification i{
  margin-right: 10px;
}


.SCtitle{
  display: flex;
  align-items: center;
  color: gray;
  font-size: 25px;
}

.SCtitle p{
  margin: 0 !important;
  color: gray;
}

.contentSubText.available{
  opacity: 1 !important;
}



.SCcta{
    align-self: center;
  }

.SCcta #subItemSC, .SCcta #ClassicView{
  transform: scale(0.9);
}



.SCcta.send .botonForm.primary{
  background: #59B200 !important;
  opacity: 0.4;
}

.SCcta.confirmed, .SCcta.rejected{
  border-color: transparent !important;
}

.contentSubItem.confir, .SCcta.confirmed{
  animation-name: SCconfirmed;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes SCconfirmed{
  0%{
    opacity: .8
  }
  50%{
    opacity: .4
  }
  100%{
    opacity: .8
  }
}


.anim-flow{

  animation-name: animFlow;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes animFlow{
  0%{
    transform: translateX(0);
  }
  50%{
    transform: translateX(5px);
  }
  100%{
    transform: translateX(0);
  }
}

.loaderScontainer .lds-roller div:after{
  background: green !important;
}


.enviarNero{
  margin-right: 10px;
  font-size: 18px;
  animation-name: enviarNero;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes enviarNero{
  0%{
    transform: translateY(0px);
    opacity: 0;
  }
  50%{
    opacity: 1
  }
  100%{
    transform: translateY(40px);
    opacity: 0
  }
}


.loaderScontainer{
  position: relative;
  width: 30px
}
.confirmedIndentSc{
  display: flex;
  color: green ;
}

.confirmedIndentSc .lds-roller{
  transform: scale(0.3);
  height: auto !important;
  width: auto !important;
}

.contentSubText{
  color: gray;
  display: grid;
  align-items: center;
  padding-left: 20px;
}

.SCimgItem{
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
}

.ScimgClassicView{
  display: flex;
  align-items: center;
  justify-content: center;
}

.ScimgClassicView.classic_view svg{
    margin-right: 10px;
    transform: scale(0.7);
}

.SCimgItemCont{
  display: grid;
  align-items: center;
  justify-items: center;
  background: white;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 1px solid #d5d5d6;
  z-index: 1;
  box-shadow: 1px 1px 1px 1px grey;
  transform: scale(.94);
}

.SCimgItemCont.first{
  transform: scale(1.1);
}

.SCimgItemCont.active{
  border: 2px solid #1babec;
      box-shadow: none;
}

.SCconector{
  position: absolute;
  width: 7px;
  background: #d5d5d6;
  height: 100px;
  bottom: -40%;
}

.SCconector.active{
  background: linear-gradient(to bottom, #00D2FF, #3A7BD5);
}

.SCconector.last{
  display: none;
}

.SCUnverify{
  /*color:red !important;*/
}






.SecurityCenter{
  width: 100%;
  height: 100%;
}
